import React, { Component } from "react";
import { getMovies } from "../services/fakeMovieService";
import Table from "./table";
import Navbar from "./navbar";
import paginate from "../utils/pagination";
import Filter from "./common/filtering";
import Pagination from "./common/pagination";
import LiveSearchBox from "./common/searchBox";
import _ from "lodash";
import { Link } from "react-router-dom";

class Movies extends Component {
  state = {
    movies: [],
    pageSize: 7,
    currentPage: 1,
    filter: "all",
    search: "",
    sortColumn: { path: "title", order: "asc" },
  };

  componentDidMount() {
    this.setState({ movies: getMovies() });
  }
  render() {
    const { movies, pageSize, currentPage, filter, sortColumn, search } =
      this.state;

    let filteredMovies = movies;
    if (search) {
      filteredMovies = movies.filter((m) => {
        return m.title.toLowerCase().includes(search.toLowerCase());
      });
    } else if (filter) {
      filteredMovies =
        filter === "all"
          ? movies
          : movies.filter((m) => m.genre.name === filter);
    }

    // ;
    // we use the lodash method orderBy to sort, _.orderBy(the array to sort, [the path to sort by], [the order to sort by])
    const sorted = _.orderBy(
      filteredMovies,
      [sortColumn.path],
      [sortColumn.order]
    );
    const pageMovies = paginate(sorted, currentPage, pageSize);

    return (
      <React.Fragment>
        <div className="row">
          <div className="col-3">
            <Filter onFilter={this.handleFilter} filter={this.state.filter} />
          </div>
          <div className="col-sm">
            <div className="row">
              <div className="col">
                <div className="col">
                  <div className="row justify-content-center">
                    <div className="col-2">
                      <Link to="/movies/new" className="btn btn-primary w-100">
                        New Movie
                      </Link>
                    </div>
                    <div className="col-6">
                      <LiveSearchBox
                        value={search}
                        onChange={this.handleSearch}
                      />
                    </div>
                  </div>
                </div>
                <h3>{this.movieNumber()}</h3>
                <Table
                  movies={movies}
                  pageMovies={pageMovies}
                  sortColumn={sortColumn}
                  onDelete={this.handleDelete}
                  onLike={this.handleLike}
                  onSort={this.handleSorting}
                  filteredMovies={filteredMovies}
                />
              </div>
            </div>
            <div className="row">
              <Pagination
                itemsCount={filteredMovies.length}
                onPageChange={this.handlePageChange}
                pageSize={pageSize}
                currentPage={currentPage}
              />
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
  handleDelete = (id) => {
    const movies = this.state.movies.filter((m) => m._id !== id);
    this.setState({ movies });
  };

  movieNumber() {
    const { filter, movies } = this.state;
    const filteredMovies =
      filter === "all" ? movies : movies.filter((m) => m.genre.name === filter);
    const number = filteredMovies.length;
    return number > 0
      ? "showing " + number + " movies in database"
      : "there is no movie in database";
  }

  handleLike = (movie) => {
    const movies = this.state.movies;
    const index = movies.indexOf(movie);
    movies[index] = { ...movie };
    if (movie.liked) {
      movies[index].liked = false;
    } else {
      movies[index].liked = true;
    }
    this.setState({ movies });
  };

  handlePageChange = (page) => {
    this.setState({ currentPage: page });
  };

  handleFilter = (genre) => {
    this.setState({ filter: genre, currentPage: 1, search: "" });
  };

  handleSorting = (sortColumn) => {
    this.setState({ sortColumn });
  };

  handleSearch = (search) => {
    this.setState({ search, filter: "all", currentPage: 1 });
  };
}

export default Movies;
