import React, { Component } from 'react';
import {movies} from '../services/fakeMovieService'
import { faHeart } from "@fortawesome/free-solid-svg-icons";
import { faHeart as emptyHeart} from "@fortawesome/free-regular-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import TableHeader from './common/tableHeader';
import TableBody from './common/tableBody';
import {Link} from 'react-router-dom';

class Table extends Component {

    liked (m) {
        return m.liked ? <FontAwesomeIcon icon={faHeart} /> : <FontAwesomeIcon icon={emptyHeart} />;
    };
    

  render() { 
    const columns = [
      {path:'title', label:'Title', content: m => <Link to={`/movies/${m._id}`}>{m.title}</Link>},
      {path:'genre.name', label:'Genre'},
      {path:'numberInStock', label:'Stock'},
      {path:'dailyRentalRate', label:'Rate'},
      {key:'like', content: m => <a style={{cursor: "pointer"}} onClick={()=>this.props.onLike(m)}>{this.liked(m)}</a>},
      {key:'delete', content: m => <button onClick={() => this.props.onDelete(m._id)} className='btn btn-danger btn-sm'>Delete</button>},
]

      const { onSort, sortColumn, pageMovies} = this.props
      return (
      <React.Fragment>
        <div className="row">
            <div className="col">
                <table className="table">
                <TableHeader onSort = {onSort} sortColumn = {sortColumn} columns = {columns} />
                  <TableBody data={pageMovies} columns={columns} liked = {this.liked} />
                </table>
            </div>
        </div>
        <div className="row">
          <div className="col">
              
            </div>
        </div>

      </React.Fragment>
      );
  }
  

}
 
export default Table;
