import React, { Component } from "react";
import Navbar from "./navbar";
import { Route, Routes, Navigate } from "react-router-dom";
import Movies from "./movies";
import Customers from "./customers";
import Pricing from "./pricing";
import MovieForm from "./functionMovieForm";
import NotFoundPage from "./common/notFoundPage";
import LogInForm from "./common/logInForm";
import "./app.css";
import RegisterForm from "./common/registerForm";
import { BrowserRouter } from "react-router-dom";

class App extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <BrowserRouter>
          <div className="row">
            <div className="col">
              <Navbar />
            </div>
          </div>

          <div>
            <Routes>
              <Route path="/movies/:id" element={<MovieForm />} />
              {/* <Route path='/addMovie' element={<MovieForm />}/> */}
              <Route path="/movies" element={<Movies />} />
              <Route path="/customers" element={<Customers />} />
              {/* we use render to pass props to an element, and we make sure to spread the props */}
              <Route path="/pricing" element={<Pricing />} />
              <Route path="/login" element={<LogInForm />} />
              <Route path="/register" element={<RegisterForm />} />
              {/* <Route path='/not-found' element={<NotFoundPage />}/>
            <Route
                    path="/redirect"
                    element={ <Navigate to="/error-page" /> } */}
              {/* /> */}
              <Route path="*" element={<NotFoundPage />} replace />
              <Route path="/" element={<Movies />} replace />
            </Routes>
          </div>
        </BrowserRouter>
      </React.Fragment>
    );
  }
}

export default App;
